<template>
  <div class="dataList-container" style="width: 100%;padding: 20px">
    <el-page-header content="预览" @back="goBack"></el-page-header>
    <div style="padding-top: 20px">
      <el-form :model="queryForm" inline size="small">
        <el-form-item
            v-for="(item, index) in queryList"
            :key="index"
            :label="item.label"
        >
          <template v-if="item.type == 7">
            <el-date-picker
                v-model="queryForm[item.prop]"
                type="daterange"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                value-format="yyyy-MM-dd"
            ></el-date-picker>
          </template>
          <template
              v-if="
              item.type == 'option' ||
              item.type == 'options' ||
              item.type == 'checkbox' ||
              item.type == 'radio'
            "
          >
            <el-select
                v-model="queryForm[item.prop]"
                filterable
                clearable
                multiple
                collapse-tags
                @change="$forceUpdate()"
            >
              <el-option
                  v-for="(optionitem, optionindex) in item.optionList"
                  :key="optionindex"
                  :value="optionitem.val"
                  :label="optionitem.name"
              ></el-option>
            </el-select>
          </template>
          <template v-if="item.type == 'sql' || item.type == 'sqls'">
            <el-select
                v-model="queryForm[item.prop]"
                filterable
                clearable
                remote
                multiple
                collapse-tags
                :remote-method="(val) => remoteMethod(val, item)"
                @change="$forceUpdate()"
            >
              <el-option
                  v-for="(optionitem, optionindex) in item.optionList"
                  :key="optionindex"
                  :value="optionitem.val"
                  :label="optionitem.name"
              ></el-option>
            </el-select>
          </template>
          <template
              v-if="
              item.type == 1 ||
              item.type == 2 ||
              item.type == 3 ||
              item.type == 4
            "
          >
            <el-input v-model="queryForm[item.prop]"></el-input>
          </template>
          <template v-if="item.type == 8">
            <el-date-picker
                v-model="queryForm[item.prop]"
                type="datetimerange"
                range-separator="至"
                unlink-panels
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                value-format="yyyy-MM-dd HH:mm:ss"
            ></el-date-picker>
          </template>
          <template v-if="item.type == 9">
            <el-date-picker
                v-model="queryForm[item.prop]"
                type="year"
                placeholder="选择年"
                value-format="yyyy"
            ></el-date-picker>
          </template>
          <template v-if="item.type == 10">
            <el-date-picker
                v-model="queryForm[item.prop]"
                type="month"
                placeholder="选择月"
                value-format="yyyy-MM"
            ></el-date-picker>
          </template>
          <template v-if="item.type == 11">
            <el-time-picker
                v-model="queryForm[item.prop]"
                is-range
                range-separator="至"
                start-placeholder="开始时间"
                end-placeholder="结束时间"
                placeholder="选择时间范围"
                value-format="HH:mm:ss"
            ></el-time-picker>
          </template>
        </el-form-item>

        <el-form-item v-if="flowFlag" label="流程状态">
          <el-select
              v-model="queryForm['toexamine_result']"
              filterable
              clearable
              style="width: 100px"
          >
            <el-option :value="9" label="完成"></el-option>
            <el-option :value="0" label="未开始"></el-option>
            <el-option :value="1" label="进行中"></el-option>
            <el-option :value="-1" label="驳回"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button
              type="primary"
              icon="el-icon-search"
              @click="loadTableData"
          >
            查询
          </el-button>
        </el-form-item>
        <el-form-item>
          <el-button
              type="primary"
              size="small"
              icon="el-icon-plus"
              @click="addNewRecord"
          >
            新建
          </el-button>
        </el-form-item>
        <el-form-item>
          <el-button
              type="primary"
              size="small"
              icon="el-icon-download"
              @click="handleDownload"
          >
            导出
          </el-button>
        </el-form-item>
      </el-form>
    </div>

    <div ref="tableContainer">
      <div style="height: 100%">
        <SimpleTable
          ref="SimpleTable"
          :columns="columns"
          :data="tableData"
          :pagination="page"
          :show-check-box="true"
          height="auto"
          table-size="small"
          table-width="100% !important"
          @handleSizeChange="handleSizeChange"
          @handleCurrentChange="handleCurrentChange"
        >
        </SimpleTable>
      </div>

      <!-- 注意：必须同时设置v-if和close-on-press-escape属性，保证FormWidget在每一次对话框显示时都重新初始化！！ -->
      <el-dialog
          v-if="showFormDialogFlag"
          :title="formTitle"
          :visible.sync="showFormDialogFlag"
          :destroy-on-close="true"
          :close-on-click-modal="false"
          :show-close="true"
          :close-on-press-escape="false"
          :before-close="handleClose"
      >
        <el-alert
            v-if="oneToOneChilds.length"
            title="子表单"
            type="success"
            :closable="false"
            style="margin: -30px -20px 20px -20px; width: auto"
        >
          <div slot="title">
            <template v-for="(item, index) in oneToOneChilds">
              <el-tag :key="index" closable @close="handleRemove(item)">
                <el-button
                    size="mini"
                    type="text"
                    style="padding: 0"
                    @click="toSubForm(item)"
                >
                  {{ item.table_name_zh }}
                </el-button>
              </el-tag>
            </template>
          </div>
        </el-alert>
        <FormWidget
            ref="formWidget"
            :entity="entity"
            :layout="layout"
            :form-model="formModel"
            :field-props-map="fieldPropsMap"
            :dataid="data_id"
            :labels-model="labelsModel"
            :form-state="formState"
            :userrole="userRole"
            :one-to-one-childs="oneToOneChilds"
            @setFormModel="setValue"
            @setChildrens="setChildrens"
        ></FormWidget>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="hadelCancel">取 消</el-button>
          <el-button
              v-if="formState == 3"
              type="primary"
              size="small"
              style="width: 90px"
              @click="hadelPrint"
          >
            打 印
          </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import FormWidget from "@/views/system/field-widget/form-widget";
import FormState from "@/views/system/form-state-variables";
import {
  initDataList,
  createRecord,
  updateRecord,
  saveRecord,
  deleteRecord,
  getDataList,
  getColumnShowNoPage,
} from "@/api";
import { arrayContain, isEmptyStr, setColumnFormatter } from "@/utils/util";
import Vue from "vue";
import { createLayoutObj } from "@/views/system/layout/form-layout-object";
export default {
  name: "DataList",
  components: { FormWidget },
  props: {
    entity: {
      type: Object,
      default: () => {},
    },
    customAddAction: {
      type: Boolean,
      default: false,
    },
    customEditAction: {
      type: Boolean,
      default: false,
    },
    customDeleteAction: {
      type: Boolean,
      default: false,
    },
    customDataLoad: {
      type: Boolean,
      default: false,
    },
    page: {
      type: Object,
      default: () => {
        return {
          pageNo: 1,
          limit: 20,
          sizes: [10, 20, 30, 50, 100],
          total: 0,
        };
      },
    },

    crudMethods: {
      type: Object,
      default: () => {
        return {
          initDataList: initDataList,
          createRecord: createRecord,
          updateRecord: updateRecord,
          saveRecord: saveRecord,
          deleteRecord: deleteRecord,
        };
      },
    },
  },
  data() {
    return {
      value: '',
      dialogVisible3: false,
      oneToOneChilds: [],
      dialogVisible: false,
      entityLabel: '',
      idFieldName: null,
      nameFieldName: null,
      curRecordId: null,

      keyword: '',
      searchFilter: '',

      columns: [],
      tableData: [],

      showFormDialogFlag: false,
      layout: {},
      formState: 1,
      formModel: {},
      labelsModel: {},
      fieldPropsMap: {},
      formModelCopy: {},

      queryForm: {},
      queryList: [],
      flowData: [],
      flowFlag: false,
      userRole: null,
      data_id: null,
      selectRow: null,
    };
  },
  computed: {
    tableHeight() {
      return this.$baseTableHeight();
    },
    formTitle() {
      if (this.formState === FormState.NEW) {
        return "新建" + this.entityLabel;
      } else if (this.formState === FormState.EDIT) {
        return "编辑" + this.entityLabel;
      } else {
        return this.entityLabel + "详情";
      }
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.resizeTableHeight();
    });
    window.onresize = () => {
      this.resizeTableHeight();
    };

    this.initTable();
  },
  methods: {
    goBack() {
      this.$router.back(-1);
    },
    handleDownload() {

    },
    setChildrens(data) {
      let list = this.oneToOneChilds.filter(
          (o) => o.table_id == data.table_id
      )
      if (list.length <= 0) {
        this.oneToOneChilds.push(data)
      }
    },
    hadelCancel() {
      this.showFormDialogFlag = false
    },
    async remoteMethod(val, item) {
      const { t } = await getDataBySql({ val }, item.type_content)
      this.$set(item, 'optionList', t)
      this.$forceUpdate()
    },
    setValue(value) {
      this.formModel[value.key] = value.value;
    },

    async handleExecute(data, done) {
      const { t, state, msg } = await executeToexamine(data);
      this.$message({ type: state, message: msg });
      this.loadTableData();
      done();
    },
    resizeTableHeight() {
      /* table自适应高度 */
      // this.$nextTick(() => {
      //   if (this.$refs.tableContainer.$el) {
      //     this.tableHeight = this.$refs.tableContainer.$el.offsetHeight - 42;
      //     console.log(this.tableHeight);
      //   }
      // });
    },
    async getQueryForm() {
      const { t, state, msg } = await getColumnShowNoPage({
        table_id: this.entity.id,
      });
      if (state == "success") {
        this.queryList = t ? t : [];
        // for (let i = 0; i < this.queryList.length; i++) {
        //   this.queryForm[this.queryList[i].prop] = ''
        // }
      } else {
        this.$message({
          message: msg,
          type: "error",
        });
      }
    },
    async initTable() {
      if (!this.entity) {
        this.$message.error("entity of prop is null");
        return;
      }

      if (this.customDataLoad === true) {
        return;
      }
      const res = await this.crudMethods.initDataList({
        table_id: this.entity.id,
        table_name: this.entity.name,
        pageNum: this.page.pageNo,
        pageSize: this.page.limit,
      });
      if (res.state == "error") {
        this.$message({ message: res.msg, type: "error" });
        return;
      }
      this.queryList = res.t && res.t.conditionList ? res.t.conditionList : []
      this.$set(this.page, 'total', res.t.pagination.total)
      // this.page.total = res.t.pagination.total
      this.setTableData(res.t.columnList, res.t.dataList);
      if (res && res.t && res.t.columnList && res.t.columnList.length > 0) {
        this.flowFlag =
          res.t.columnList[res.t.columnList.length - 1].prop ==
          "toexamine_result"
            ? true
            : false;
      }
    },

    /**/
    setTableData(columnList, recordList) {
      this.columns = columnList;
      this.columns.forEach((col) => {
        setColumnFormatter(col);
      });
      this.tableData = recordList;
      this.$refs.SimpleTable.tabeldata = recordList;
      this.$refs.SimpleTable.columns = columnList;
    },

    /* 保持查询条件，重新当前页加载数据 */
    loadTableData(filter) {
      let queryForm = {};
      _.forEach(this.queryForm, (val, key) => {
        if (_.isArray(val)) {
          if (val.length > 0) {
            queryForm[key] = val;
          }
        } else if (val) {
          queryForm[key] = val;
        }
      });

      getDataList(
        {
          table_id: this.entity.id,
          table_name: this.entity.name,
          pageSize: this.page.limit,
          pageNum: this.page.pageNo,
        },
        queryForm
      )
        .then((res) => {
          if (res.state == "error") {
            this.$message({ message: res.msg, type: "error" });
          } else {
            this.tableData = res.t.dataList;
            this.$refs.SimpleTable.tabeldata = this.tableData;
            // this.page.total = res.t.pagination.total
            this.$set(this.page, 'total', res.t.pagination.total)
            this.$set(this.page, 'pageNo', res.t.pagination.pageNo)
          }
        })
        .catch((res) => {
          this.$message({ message: res.msg, type: "error" });
        });
    },

    // 改变分页大小处理
    handleSizeChange(val) {
      if (this.customDataLoad === true) {
        this.$emit("handleSizeChange", val); //父组件发出消息，由父组件处理
        return;
      }

      this.$set(this.page, 'limit', val)
      this.loadTableData(this.searchFilter)
    },

    // 翻页处理
    handleCurrentChange(val) {
      if (this.customDataLoad === true) {
        this.$emit("handleCurrentChange", val); //父组件发出消息，由父组件处理
        return;
      }

      this.$set(this.page, 'pageNo', val)
      this.loadTableData(this.searchFilter)
    },

    searchData() {
      if (this.customDataLoad === true) {
        this.$emit("searchData", this.keyword); //父组件发出消息，由父组件处理
        return;
      }

      if (!this.nameFieldName) {
        this.$message.error(
          "当前实体[" + this.entity + "]未指定名称字段，不能搜索！"
        );
        return;
      }

      let searchField = this.nameFieldName;
      let searchWord = this.keyword;
      this.searchFilter = `([${searchField}] like '%${searchWord}%')`;
      this.loadTableData(this.searchFilter);
    },

    clearSearch() {
      if (this.customDataLoad === true) {
        this.keyword = "";
        this.$emit("clearSearch"); //父组件发出消息，由父组件处理
        return;
      }

      this.searchFilter = "";
      this.loadTableData();
    },

    refreshTableData() {
      if (this.customDataLoad === true) {
        this.$emit("refreshTable"); //父组件发出消息，由父组件处理
        return;
      }

      this.loadTableData(this.searchFilter);
    },

    addNewRecord() {
      if (this.customAddAction === true) {
        this.$emit('addTableRecord') //父组件发出消息，由父组件处理
        return
      }

      this.crudMethods
          .createRecord({
            table_id: this.entity.id,
            table_name: this.entity.name,
          })
          .then(async (res) => {
            if (res.state == 'error') {
              this.$message({ message: res.error, type: 'error' })
              return
            }
            if (!!res.t && !!res.t.layoutJson) {
              this.curRecordId = null
              this.layout = this.buildLayoutObj()
              this.layout.setLayoutPropsFromServer(res, 'add')
              this.handleDeletedFields(res) /* 处理表单已删除字段！！ */
              this.formModel = res.t.formData
              this.formModelCopy = JSON.parse(JSON.stringify(res.t.formData))
              this.labelsModel = res.t.labelData
              this.fieldPropsMap = res.t.fieldPropsMap
              this.formState = FormState.NEW
              this.showFormDialogFlag = true
              if (this.$refs['formWidget']) {
                this.$refs['formWidget'].clearFormValidate()
              }
              this.oneToOneChilds = []
            } else {
              this.$message({ message: '加载表单布局出错', type: 'error' })
            }
          })
          .catch((res) => {
            this.$message({ message: res.message, type: 'error' })
          })
    },

    buildLayoutObj() {
      let eventHub = new Vue();
      return createLayoutObj(eventHub);
    },

    handleDeletedFields(res) {
      let deletedFields = res.t.deletedFields;
      if (!!deletedFields && Array.isArray(deletedFields)) {
        let layoutFieldWrappers = this.layout.getAllLayoutFieldWrappers();
        if (!!layoutFieldWrappers && Array.isArray(layoutFieldWrappers)) {
          layoutFieldWrappers.forEach((fw) => {
            let deletedFlag = arrayContain(deletedFields, fw.name);
            if (deletedFlag) {
              fw.deleted = true; /* 设置已删除字段标志 */
            }
          });
        }
      }
    },

    editTableData(row) {
      if (this.customEditAction === true) {
        this.$emit("editTableRow", row); //向父组件发出消息，由父组件处理
        return;
      }

      this.curRecordId = row[this.idFieldName];
      this.crudMethods
        .updateRecord({
          data_id: row.id,
          table_id: this.entity.id,
          table_name: this.entity.name,
        })
        .then((res) => {
          if (res.state == "error") {
            this.$message({ message: res.msg, type: "error" });
            return;
          }

          if (!!res.t && !!res.t.layoutJson) {
            this.layout = this.buildLayoutObj();
            this.layout.setLayoutPropsFromServer(res, "add");
            this.handleDeletedFields(res); /* 处理表单已删除字段！！ */
            this.formModel = res.t.formData;
            this.formModelCopy = JSON.parse(JSON.stringify(res.t.formData));
            this.labelsModel = res.t.labelData;
            this.fieldPropsMap = res.t.fieldPropsMap;
            this.formState = FormState.EDIT; //编辑记录状态
            this.showFormDialogFlag = true;
            if (this.$refs["formWidget"]) {
              this.$refs["formWidget"].clearFormValidate();
            }
          } else {
            this.$message({ message: "加载表单布局出错", type: "error" });
          }
        })
        .catch((res) => {
          this.$message({ message: res.message, type: "error" });
        });
    },

    hadelSave(postData) {
      this.crudMethods
        .saveRecord(
          {
            table_id: this.entity.id,
            table_name: this.entity.name,
            data_id: this.formModel.id,
            operation_type: this.formModel.id ? 2 : 1,
          },
          postData
        )
        .then((res) => {
          if (res.state == "error") {
            this.$message({ message: res.msg, type: "error" });
            return;
          }
          // console.log(res);
          // this.formModel = res.t.formData;
          // this.labelsModel = res.t.labelData;
          this.$message({ message: "保存成功", type: "success" });
          this.showFormDialogFlag = false;
          this.loadTableData(this.searchFilter);
        })
        .catch((res) => {
          this.$message({ message: res.msg, type: "error" });
        });
    },
    deleteTableData(row) {
      if (this.customDeleteAction === true) {
        this.$emit("deleteTableRow", row); //向父组件发出消息，由父组件处理
        return;
      }

      this.$confirm("是否删除该数据?", "删除确认")
        .then(() => {
          this.crudMethods
            .deleteRecord({
              data_id: row.id,
              table_id: this.entity.id,
              table_name: this.entity.name,
            })
            .then((res) => {
              if (res.state == "error") {
                this.$message({ message: res.msg, type: "error" });
                return;
              }

              this.$message.success("删除成功");
              this.loadTableData(this.searchFilter);
            })
            .catch((res) => {
              this.$message({ message: res.message, type: "error" });
            });
        })
        .catch(() => {
          this.$message.info("取消删除");
        });
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
  },
};
</script>

<style lang="scss" scoped>
.dataList-container {
  ::v-deep .el-main {
    padding: 0;
  }
}

.el-container {
  padding: 10px;
  header {
    border: 0;
  }
}
.list-search-panel {
  height: auto !important;
  //line-height: 62px;
  //border-left: 1px solid #ebeef5;
  margin-bottom: 0;
  .el-form--inline {
    display: inline-flex;
    align-content: center;
    .el-form-item__content {
      vertical-align: middle;
    }
  }
}

.list-search-panel:after {
  content: "";
  display: block;
  clear: both;
}

.search-panel-left {
  float: left;
}

.search-panel-right {
  float: right;

  /* 解决IE浮动元素垂直居中问题 begin */
  position: relative;
  ::v-deep .el-form-item {
    margin-bottom: 0;
  }
  ::v-deep .el-input.v-middle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  /* end */
}
</style>
